import * as React from "react";
import { Button, ButtonType } from "office-ui-fabric-react";
import Header from "./Header";
import HeroList, { HeroListItem } from "./HeroList";
import Progress from "./Progress";
import wordStyles from "./wordStyles/styles"

export default class App extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      listItems: []
    };
  }

  componentDidMount() {
    this.setState({
      listItems: [
        {
          icon: "Ribbon",
          primaryText: "Achieve more with Office integration"
        },
        {
          icon: "Unlock",
          primaryText: "Unlock features and functionality"
        },
        {
          icon: "Design",
          primaryText: "Create and visualize like a pro"
        }
      ]
    });
  }

  click = async () => {
    return Word.run(async context => {
      // insert a paragraph at the end of the document.
      const generalText = require("../text/generalText.json");
      const titleAnamnesis = context.document.body.insertParagraph(
        generalText.titleAnamnesis,
        Word.InsertLocation.end
      );
       titleAnamnesis.font.set(wordStyles.title);

      const anamnesis = context.document.body.insertParagraph(
            generalText.anamnesis,
            Word.InsertLocation.end
        );
       anamnesis.font.set(wordStyles.text);
        await context.sync();
    });
  };

  render() {
    const { title, isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return (
        <Progress
          title={title}
          logo="assets/logo-filled.png"
          message="Please sideload your addin to see app body."
        />
      );
    }

    return (
      <div className="ms-welcome">
        {/* <Header
          logo="assets/logo-filled.png"
          title={this.props.title}
          message="Welcome"
        />*/}
        <HeroList
          message="Discover what Office Add-ins can do for you today!"
          items={this.state.listItems}
        >
          <p className="ms-font-l">
            Modify the source files, then click <b>Run</b>.
          </p>

        </HeroList>

          <Button
              className="ms-welcome__action"
              buttonType={ButtonType.hero}
              iconProps={{ iconName: "ChevronRight" }}
              onClick={this.click}
          >
              Run
          </Button>
      </div>
    );
  }
}
